@import "../style/variables";

.section-slider {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	padding: 23px 0px;
	position: relative;

	&__left {
		flex: 0 0 65px;
		padding: 0 15px;
		&:before {
			content: '\e92e';
			font-family: 'icomoon';
			color: $pink;
			font-size: 33px;
			vertical-align: middle;
			display: inline-block;
			cursor: pointer;
		}
	}

	&__right {
		flex: 0 0 65px;
		padding: 0 15px;
		&:before {
			content: '\e92f';
			font-family: 'icomoon';
			color: $pink;
			font-size: 33px;
			vertical-align: middle;
			display: inline-block;
			cursor: pointer;

		}
	}

	&__slider {
		flex: 1 1 auto;
	}

	&--active {
		.section-slider__right {
			animation-name: bounce;
  			animation-duration: .8s;
  			animation-iteration-count: infinite;
  			animation-timing-function: linear;
		}
		.section-slider__left {
			animation-name: bounce;
  			animation-duration: .35s;
  			animation-iteration-count: infinite;
  			animation-timing-function: linear;
		}
	}

	&__warning {
		color: $pink;
		font-size: 12px;
		position: absolute;
		bottom: 5px;
		left: 5px;
	}

	&__warning.slowest {
		text-align: right;
		right: 5px;
		left: auto;
	}
}

@keyframes slider {
    0% {transform: scale(1);}
    50% {transform: scale(1.3)}
    100% {transform: scale(1)}
}

@keyframes bounce {
    0% {transform: translateY(0);}
    25% {transform: translateY(-15%)}
    75% {transform: translateY(15%)}
    100% {transform: translateY(0)}
}

