@import "../style/variables";
@import "../style/animations";

.Accept {
  background: $pink;
  border-radius: 3px;
  @include flash(infinite, $duration: 5s);
}

.Reject {
  position: relative;
  background: white;
  border: 3px dashed $pink;
  border-radius: 3px;

  &:after {
    content: "x";
    position: absolute;
    color: $pink;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    font-size: 150px;
    line-height: 300px;
    text-align: center;
    @include flash(infinite);
  }
}

.Error {
  color: $pink;
  text-align: center;
  margin: 20px;
}
.ProgressHolder {
  border: 1px solid $pink;
  width: 200px;
  height: 1em;
  margin: 100px auto 10px;
}

.ProgressBar {
  background: $pink;
  display: block;
  height: 100%;
}

.Uploading {
  text-align: center;
  color: $pink;
  @include flash(infinite, 5s);
}
