@import "./style/variables.scss";

.App {
  width: 400px;
  background: white;

  margin: 3em auto;
  border-radius: 15px;
  padding: 15px;
  color: $dark;
}

.Header {
  grid-area: Header;

  h3 {
    text-align: center;
    text-transform: uppercase;
    color: $pink;
    margin: 0;
  }
}

.Dropzone {
  width: 400px;
  height: 300px;
  border: 3px dashed $pink;
  border-radius: 3px;
}

.UploadingContainer {
  width: 400px;
  height: 300px;
}

.UploadMessage {
  text-align: center;
  line-height: 300px;

}

.FileContainer {
  width: 400px;
  height: 300px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 5px;
  grid-template-areas: "FileInfo Price" "FileInfo Time" "FileInfo Help" "Slider Slider" "Continue Continue";
}


.FileInfo { grid-area: FileInfo; }

.Price { grid-area: Price; }

.Time { grid-area: Time; }

.Help {
  grid-area: Help;
  border-top: 2px solid $dark;
  text-align: center;
}

.Slider { grid-area: Slider; }

.Continue { grid-area: Continue;}

.Error {
  color: $pink;
  text-align: center;
  margin: 20px;
}
