@import "../style/variables";

.holder {
  text-align: center;
  color: white;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  background: $pink;
  border: 2px dashed rgba(66, 66, 66, 0.32);
  border-radius: 5px;
  cursor: pointer;
  overflow: hidden;
}

.info {
  flex-grow: 1;

  color: white;
}

.activeDropzone {
  background: darken($pink, 10%);
  border-color: transparent;
}

.Image {
  margin-top: 1em;
}

.Name {

}

.WordCount {
  color: $grey-3;
  margin-top: auto;
  text-align: center;
  font-size: smaller;
}
